import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';
import TileWMS from 'ol/source/TileWMS';
import 'ol/ol.css';
import GeoJSON from 'ol/format/GeoJSON';
import LinearRing from 'ol/geom/LinearRing';
import VectorSource from 'ol/source/Vector';
import {
  LineString,
  MultiLineString,
  MultiPoint,
  MultiPolygon,
  Point,
  Polygon,
} from 'ol/geom';
import { Vector as VectorLayer} from 'ol/layer';
import {fromLonLat} from 'ol/proj';

var source2 = new VectorSource();
fetch('./data/geojson/citizens.geojson')
  .then(function (response) {
    return response.json();
  })
  .then(function (json) {
    var format = new GeoJSON();
    var features = format.readFeatures(json, {
      featureProjection: 'EPSG:4326',
    });

    var parser = new jsts.io.OL3Parser();
    parser.inject(
      Point,
      LineString,
      LinearRing,
      Polygon,
      MultiPoint,
      MultiLineString,
      MultiPolygon
    );

    for (var i = 0; i < features.length; i++) {
      var feature = features[i];
      // convert the OpenLayers geometry to a JSTS geometry
      var jstsGeom = parser.read(feature.getGeometry());

      // create a buffer of 40 meters around each line
      var buffered = jstsGeom.buffer(40);

      // convert back from JSTS and replace the geometry on the feature
      feature.setGeometry(parser.write(buffered));
    }

    source2.addFeatures(features);
  });


var layers = [
  new TileLayer({
    source: new OSM()
  }),
  new VectorLayer({
    source: source2,
  }),
  new TileLayer({
   
    source: new TileWMS({
      url: 'http://localhost:8080/geoserver/moscow/wms',
      params: {'LAYERS': 'moscow:border', 'TILED': true, 'TRANSPARENT':true},
      serverType: 'geoserver',
      // Countries have transparency, so do not fade tiles:
      transition: 0
    })
  }), new TileLayer({
   
    source: new TileWMS({
      url: 'http://localhost:8080/geoserver/moscow/wms',
      params: {'LAYERS': 'moscow:citizens', 'TILED': true},
      serverType: 'geoserver',
      // Countries have transparency, so do not fade tiles:
      transition: 0
    })
  }), new TileLayer({
   
    source: new TileWMS({
      url: 'http://localhost:8080/geoserver/moscow/wms',
      params: {'LAYERS': 'moscow:poi', 'TILED': true},
      serverType: 'geoserver',
      // Countries have transparency, so do not fade tiles:
      transition: 0
    })
  })
];

var map = new Map({
  layers: layers,
  target: 'map',
  view: new View({
    center:fromLonLat( [37.520193,55.613960]),
    zoom: 13
    
  })
});